import React, { ReactNode, useCallback, useEffect, useMemo, useState } from 'react';
import { DownOutlined, InfoCircleOutlined } from '@ant-design/icons';
import { Button, Card, Checkbox, Col, DatePicker, Form, FormProps, Input, Row, Select, Space, theme, Typography } from 'antd';
import './index.less';

/**
 * & È´òÁ∫ßÊêúÁ¥¢Ë°®Ê†º & Êñ∞Âª∫Ë°®ÂçïÊï∞ÊçÆ Form
 * @param props
 * @returns
 */
// ÂÆåÊàêÔºöÂæÖÂÆûÁé∞1ÔºöProTable Êñ∞Âª∫ÊåâÈíÆ‰ΩøÁî®Â∞ÅË£ÖÁöÑ AdvancedSearchForm ÁªÑ‰ª∂
// ÂÆåÊàêÔºöÂæÖÂÆûÁé∞2ÔºöÁî®Êà∑ÁÆ°ÁêÜ‰∏≠Ë°®Â§¥‰πüÈúÄË¶Å‰ΩøÁî® AdvancedSearchForm ÁªÑ‰ª∂
export type FormItemType = 'INPUT' | 'SELECT' | 'CHECKBOX' | 'TIME_START' | 'TIME_END';
type FormFieldItem = {
	type?: 'INPUT' | 'SELECT' | 'CHECKBOX' | 'TIME_START' | 'TIME_END';
	label: string;
	field: string;
	placeholder?: string;
	list?: any[];
	component?: React.ReactNode; // üëà ÊîØÊåÅ‰º†ÂÖ•ÂÆåÊï¥ÁªÑ‰ª∂
};
type FormPropsType = {
	formRef: React.RefObject<HTMLDivElement>;
	name?: string; // Ë°®Á§∫ÊØè‰∏™FormË°®Ê†º„ÄÅÂøÖÈ°ª‰∏çÁõ∏Âêå
	isSearch?: boolean; // ÊòØÂê¶ÊòØË°®ÂçïÊêúÁ¥¢
	loading: boolean;
	rowCount?: number; // ÊØèË°åÁöÑÊï∞Èáè
	FormListConfig?: any[]; // Ë°®ÂçïÁöÑÈÖçÁΩÆÈ°π
	// tableYHeight: any;
	FormOnFinish: (data: any) => any; // Â∞ÜÁªìÊûú‰º†ÈÄíÂà∞Áà∂ÁªÑ‰ª∂
	SetIsExpand: any;
};
const AdvancedSearchForm = (Props: FormPropsType) => {
	const { formRef, name = 'advanced_search', loading, FormListConfig = [], rowCount = 3, FormOnFinish, SetIsExpand } = Props;
	const FormConfig = FormListConfig;

	const [form] = Form.useForm();
	const [expand, setExpand] = useState<boolean>(false); // ÊòØÂê¶Â±ïÂºÄ
	const [isSearch, SetIsSearch] = useState<boolean>(true); // ÊòØÂê¶ÊòØË°®ÂçïÊêúÁ¥¢

	function formateDate(time: string | number) {
		if (!time) return '';
		const date = new Date(time);
		const month = date.getMonth() < 9 ? `0${date.getMonth() + 1}` : date.getMonth() + 1;
		const data = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
		return `${date.getFullYear()}-${month}-${data}`; // * 2025-06-05
	}
	// Êèê‰∫§Ë°®ÂçïÊï∞ÊçÆ
	const OnFinish = (values: any) => {
		// Â§ÑÁêÜÂ≠óÊÆµÔºöinput„ÄÅtime„ÄÅselect„ÄÅcheckbox
		// console.log('Received values of form: ', values)
		const formValue = form.getFieldsValue();
		// console.log('formValue', formValue)
		if (formValue.startTime) formValue.startTime = formateDate(formValue.startTime);
		if (formValue.endTime) formValue.endTime = formateDate(formValue.endTime);
		// console.log('formValue', formValue)
		// console.log('value', values)
		FormOnFinish && FormOnFinish(formValue);
	};
	const OnFailed: FormProps<any>['onFinishFailed'] = errorInfo => {
		console.log('Failed:', errorInfo);
	};

	const OnExpand = () => {
		setExpand(!expand);
		SetIsExpand(!expand); // Áà∂ÁªÑ‰ª∂Â±ïÂºÄÁä∂ÊÄÅ
	};

	// * Â§ÑÁêÜ Form.Item Â≠óÊÆµ üëà
	const FormFields = useMemo(() => {
		const colsPerRow = rowCount || 3;
		const colSpan = 24 / colsPerRow;
		const collapsedCount = colsPerRow === 1 ? 1 : FormConfig.length >= colsPerRow ? colsPerRow - 1 : FormConfig.length;

		const shownList = expand ? FormConfig : FormConfig.slice(0, collapsedCount);

		const fieldNodes = shownList.map((cfg: FormFieldItem, idx: number) => {
			const { type, label, field, placeholder, list, component } = cfg;

			let inputEl: React.ReactNode = null;

			if (component) {
				// üëà ‰ºòÂÖà‰ΩøÁî®Ëá™ÂÆö‰πâÁªÑ‰ª∂ {label: "Âπ¥Êú´", component: <Input placeholder="" /> }
				inputEl = component;
			} else {
				switch (type) {
					case 'INPUT':
						inputEl = <Input placeholder={placeholder} />;
						break;
					case 'SELECT':
						inputEl = <Select placeholder={placeholder}>{/* {getOptionList(list)} */}</Select>;
						break;
					case 'CHECKBOX':
						inputEl = <Checkbox>{label}</Checkbox>;
						break;
					case 'TIME_START':
					case 'TIME_END':
						inputEl = <DatePicker showTime format='YYYY-MM-DD HH:mm:ss' placeholder={placeholder} />;
						break;
					default:
						return null;
				}
			}

			return (
				<Col span={colSpan} key={idx}>
					<Form.Item name={field} label={label}>
						{inputEl}
					</Form.Item>
				</Col>
			);
		});

		// üîΩ Ê∏≤ÊüìÊìç‰ΩúÊåâÈíÆÔºàË°•Á©∫‰Ωç + Êü•ËØ¢/ÈáçÁΩÆÔºâ
		let actionNode: React.ReactNode[] = [];
		if (isSearch) {
			const remainder = shownList.length % colsPerRow;
			const fillCount = remainder === 0 ? colsPerRow - 1 : colsPerRow - remainder - 1;

			const blankCols = Array.from({ length: fillCount }, (_, i) => <Col span={colSpan} key={`blank-${i}`} />);

			const actions = (
				<Col span={colSpan} key='actions' style={{ textAlign: 'right' }}>
					<Form.Item>
						<Space size='small'>
							<Button type='primary' htmlType='submit'>
								Êü•ËØ¢
							</Button>
							<Button onClick={() => form.resetFields()}>ÈáçÁΩÆ</Button>
							{FormConfig.length < colsPerRow ? null : (
								<Button type='link' className='text-[12px]' onClick={OnExpand}>
									{expand ? 'ÂÖ≥Èó≠' : 'Â±ïÂºÄ'} <DownOutlined />
								</Button>
							)}
						</Space>
					</Form.Item>
				</Col>
			);

			actionNode = [...blankCols, actions];
		}

		return [...fieldNodes, ...actionNode];
	}, [expand, FormConfig, rowCount, isSearch]);

	let layout = {
		labelCol: { span: 8 },
		wrapperCol: { span: 16 },
	};
	let maxWidth = { maxWidth: 600 };
	return (
		<Card id={'AdvancedSearchForm'} size='small' hoverable ref={formRef}>
			<Form size='middle' layout='horizontal' disabled={loading} name={name} form={form} variant='outlined' onFinish={OnFinish} onFinishFailed={OnFailed}>
				<Row gutter={24}>{FormFields}</Row>
			</Form>
		</Card>
	);
};

export default AdvancedSearchForm;
